.sectionHeader_burger {
  display: none;
  position: relative;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 175px;
  left: 15px;
}
.sectionHeader_burger .burger {
  margin-top: 20px;
  width: 100px;
  height: auto;
  border: none;
}
.sectionHeader_burger .burger-menu_button {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  width: 32px;
  height: 32px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  background-color: #000;
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
  background-color: #000;
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
}
.sectionHeader_burger .burger-menu_button::after, .sectionHeader_burger .burger-menu_button::before {
  content: "";
  width: 22px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 15px;
  left: 9px;
}
.sectionHeader_burger .burger-menu_button::after {
  top: auto;
  bottom: 15px;
  width: 11px;
}
.sectionHeader_burger .burger-menu_button::after, .sectionHeader_burger .burger-menu_button::before {
  transition: all 0.5s;
  content: "";
  width: 22px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 15px;
  left: 9px;
}
.sectionHeader_burger .burger-menu_button::after {
  top: auto;
  bottom: 15px;
  width: 11px;
}
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines {
  filter: brightness(0.7);
}
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines::before,
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines::after,
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines {
  background-color: rgba(0, 0, 0, 0.7);
}
.sectionHeader_burger .burger-menu_button.burger-menu_button_active:hover .burger-menu_lines {
  background-color: transparent;
}
.sectionHeader_burger .burger-menu_active .burger-menu_button::before {
  top: 19px;
  transform: rotate(45deg);
}
.sectionHeader_burger .burger-menu_active .burger-menu_button::after {
  bottom: 19px;
  width: 22px;
  transform: rotate(-45deg);
}
.sectionHeader_burger .burger-menu_nav {
  padding: 60px 30px 20px 30px;
  position: absolute;
  top: 0;
  right: -15px;
  z-index: 70;
  display: flex;
  flex-flow: column;
  background-color: #fff;
  overflow-y: auto;
  height: 372px;
  width: 323px;
  top: -400px;
  -webkit-transition: 0.8s;
  -moz-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}
.sectionHeader_burger .burger-menu_active .burger-menu_nav {
  top: -55px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.sectionHeader_burger .burger-menu_link {
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 120%;
  text-decoration: none;
  letter-spacing: 0.1em;
  color: #000;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-transform: uppercase;
}
.sectionHeader_burger .burger-menu_link:hover {
  color: #000;
}
.sectionHeader_burger .burger-menu_overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 50;
}
.sectionHeader_burger .burger-menu_active .burger-menu_overlay {
  display: block;
  background-color: rgba(0, 0, 0, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 12px;
  font-family: sans-serif;
  overflow: hidden;
}

.sectionHeader {
  padding: 16px 0 0 0;
}
.sectionHeader .sectionInner {
  width: 1170px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
}
.sectionHeader .sectionHeader_main {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 30px 0;
}
.sectionHeader .sectionHeader_main .logo {
  width: 210px;
  height: 135px;
  margin: 0 30px 0 0;
  cursor: pointer;
}
.sectionHeader .sectionHeader_main .logo img {
  width: 100%;
  height: 100%;
}
.sectionHeader .sectionHeader_main .wrap {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border: none;
  border-bottom: 1px solid #D7D7D7;
  margin: 0 0 20px 0;
  padding: 0 0 8px 0;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine .sectionHeader_main-firstLine-headNav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  color: #252525;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine .sectionHeader_main-firstLine-headNav .headNav-item {
  margin: 0 25px 0 0;
  cursor: pointer;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine .sectionHeader_main-firstLine-headNav .headNav-item:last-child {
  margin: 0;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine .sectionHeader_main-firstLine-signupIn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: #000;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine .sectionHeader_main-firstLine-signupIn .signIn {
  margin: 0 15px 0 0;
  cursor: pointer;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine .sectionHeader_main-firstLine-signupIn .sepparator {
  display: block;
  height: 15px;
  border-left: 1px solid #000;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-firstLine .sectionHeader_main-firstLine-signupIn .signUp {
  margin: 0 0 0 15px;
  cursor: pointer;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-banner {
  width: 250px;
  height: 70px;
  cursor: pointer;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-banner img {
  width: 100%;
  height: 100%;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 410px;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cartBlock_links {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 135px;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cartBlock_links .cart,
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cartBlock_links .favorite,
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cartBlock_links .compare {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  color: #252525;
  margin: 0 0 10px 0;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cartBlock_links .cart::before {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 19px;
  height: 19px;
  margin: 0 10px 0 0;
  content: url(../img/svg/cart.svg);
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cartBlock_links .favorite::before {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 19px;
  height: 19px;
  margin: 0 10px 0 0;
  content: url(../img/svg/favorite.svg);
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cartBlock_links .compare::before {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 19px;
  height: 19px;
  margin: 0 10px 0 0;
  content: url(../img/svg/compare.svg);
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cardBlock_itemsOnCart {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 240px;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-cartBlock .cardBlock_itemsOnCart-item {
  cursor: pointer;
  width: 70px;
  height: 70px;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-workTimeAndContacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: #252525;
  width: 185px;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-workTimeAndContacts .workTime {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-workTimeAndContacts .workTime .time {
  font-weight: normal;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-workTimeAndContacts .phone {
  display: inline-block;
  width: 100%;
  color: #252525;
  font-weight: bold;
  font-size: 25px;
  text-align: center;
  border-bottom: 1px solid #D7D7D7;
  padding-bottom: 15px;
  margin: 15px 0 5px 0;
  text-decoration: none;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-workTimeAndContacts .city {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-workTimeAndContacts .city .change {
  text-decoration: underline;
}
.sectionHeader .sectionHeader_main .sectionHeader_main-secondLine-workTimeAndContacts .city .change::before {
  content: url(../img/svg/change.svg);
  margin: 0 5px 0 0;
}
.sectionHeader .sectionHeader_nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: #252525;
  border-radius: 10px;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.25);
  height: 35px;
}
.sectionHeader .sectionHeader_nav-item {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-weight: bold;
  padding: 0 15px;
  color: #fff;
  transition: all 0.4s;
}
.sectionHeader .sectionHeader_nav-item:hover {
  color: #000;
  background-color: #FFCC00;
}
.sectionHeader .sectionHeader_nav-item:first-child, .sectionHeader .sectionHeader_nav-item:last-child {
  background-color: #FFCC00;
  color: #000;
}
.sectionHeader .sectionHeader_nav-item:first-child {
  border-radius: 10px 0 0 10px;
  width: 40px;
}
.sectionHeader .sectionHeader_nav-item:last-child {
  width: 130px;
  border-radius: 0 10px 10px 0;
}